* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.4;
  }
  
  a {
    color: #333;
    text-decoration: none;
  }


  .container {
    padding: 0 1rem;
    margin-right: auto;
    margin-left: 10%
  }
  
  .btn {
    display: inline-block;
    border: none;
    background: #555;
    color: #fff;
    padding: 7px 20px;
    cursor: pointer;
  }
  
  .btn:hover {
    background: #666;
  }

  .space { margin-top: 50px; }



  a {
    color: grey;
    text-decoration: none;
  }
  
  li.active > a {
    color: rgb(32, 31, 31);
  }
  
  li > a:hover {
    color: rgb(32, 31, 31);
  }


  ul.v {
    list-style-type: none;}

  .divMargs{
    margin-right: auto;
    margin-left: 100px;
  }